import * as React from 'react';
import { graphql } from 'gatsby';

import Markdown from 'react-markdown';
import rehypeRaw from 'rehype-raw';
import CarouselZoom from './carouselZoom';
import SEO from './seo';

import classNames from 'classnames/bind';
import * as styles from './project.module.scss';

const cx = classNames.bind(styles);

type Props = {
  data: GatsbyTypes.ProjectQuery;
};

const Project: React.FC<Props> = ({ data: { mpapi } }) => {
  if (!mpapi) return null;

  const project = mpapi.projects.data[0].attributes;

  const description = project.description || '';

  return (
    <>
      <SEO
        title={`${project.title} | Atelier Mendgen Peissl`}
        description={description.substring(0, 150)}
        image={project.images.data[0].attributes?.formats?.thumbnail?.url}
      />
      <CarouselZoom
        className={cx('carousel')}
        images={project.images.data}
      ></CarouselZoom>
      <h1>{project.title}</h1>
      <section className={cx('content')}>
        {description && (
          <div className={cx('description')}>
            <h2>Description</h2>
            <Markdown rehypePlugins={[rehypeRaw]}>{description}</Markdown>
          </div>
        )}
        {project.data.length > 0 && (
          <div className={cx('data')}>
            <h2>Data</h2>
            <ul className={cx('dataList')}>
              {project.data.map((data) => (
                <li key={data.title}>
                  <p>
                    <b>{data.title}:</b> {data.content}
                  </p>
                </li>
              ))}
            </ul>
          </div>
        )}
      </section>
    </>
  );
};

export const query = graphql`
  query Project($slug: String!) {
    mpapi {
      projects(filters: { slug: { eq: $slug } }) {
        data {
          attributes {
            slug
            images(pagination: { limit: 50 }) {
              data {
                attributes {
                  alternativeText
                  height
                  width
                  url
                  formats
                  hash
                }
              }
            }
            title
            placementNumber
            description
            data {
              content
              title
            }
          }
        }
      }
    }
  }
`;

export default Project;
